import Slider from "react-slick"
import React from "react";
import gsap from 'gsap';
import { Timeline } from 'gsap/gsap-core';


class Resume extends React.Component {
    
    constructor(props) {
        super(props);

        this.next = this.next.bind(this);
        this.previous = this.previous.bind(this);
    }

    next() {
        this.slider.slickNext();
    }

    previous() {
        this.slider.slickPrev();
    }

    componentDidMount() {
        gsap.registerPlugin();
        let tl = new Timeline();
        tl.set('.slick-list', {transform: 'translate(0%)'});

        this.listener = document.querySelector('.resume').addEventListener("wheel", function(e) {
            if ( e.deltaX >= 40 ) {
                this.next();
            } else if ( e.deltaX <= -40 ) {
                this.previous();
            }
        }.bind(this));
    }

    componentWillUnmount() {
        document.querySelector('.resume').removeEventListener("wheel", this.listener);
    }


    render() {

        let settings = {
            dots: true,
            infinite: false,
            slidesToShow: 1,
            slidesToScroll: 1,
            centerMode: true,
            centerPadding: '20%',
            arrows: true,
            responsive: [{
                breakpoint: 767,
                settings: {
                    centerPadding: '10%',
                }
            }]
        }


        return (
            <div className="resume">

                <div className="resume__slider">

                    <Slider ref={c => (this.slider = c)} {...settings}>
                        <div className="resume__slider__entry">
                            <div className="resume__slider__entry__container">
                                <div className="resume__slider__entry__line">
                                    <div className="resume__slider__entry__line__location">
                                        Signals Design Group
                                    </div>
                                    <div>
                                        Nov 2019 - Mar 2020
                                    </div>
                                </div>
                                <div className="resume__slider__entry__line">
                                    <div className="resume__slider__entry__line__position">
                                        Web Developer
                                    </div>
                                    <div className="resume__slider__entry__line__tech">
                                        React.js, Next.js, PHP, Wordpress, GSAP, PM2
                                    </div>
                                </div>
                                <ul className="resume__slider__entry__list">
                                    <li>Built 5 websites in React.js or Wordpress for clients from start to finish</li>
                                    <li>Implemented web animations using GSAP, AOS, CSS and Javascript</li>
                                    <li>Designed page transitions using Barba.js and next-page-transitions</li>
                                    <li>Imported After Effects animations using Lottie</li>
                                    <li>Performed SEO by updating metadata, headers and optimizing images</li>
                                    <li>Launched live and staging websites through web hosting or PM2</li>
                                </ul>
                            </div>
                        </div>
                        <div className="resume__slider__entry">
                            <div className="resume__slider__entry__container">
                                <div className="resume__slider__entry__line">
                                    <div className="resume__slider__entry__line__location">
                                        Convertus
                                    </div>
                                    <div>
                                        Aug 2018 - Nov 2019
                                    </div>
                                </div>
                                <div className="resume__slider__entry__line">
                                    <div className="resume__slider__entry__line__position">
                                        Web Developer
                                    </div>
                                    <div className="resume__slider__entry__line__tech">
                                        PHP, Wordpress, Vue.js, HTML, CSS, Javascript
                                    </div>
                                </div>
                                <ul className="resume__slider__entry__list">
                                    <li>Created three landing page templates in Wordpress for use in 1300+ sites</li>
                                    <li>Built two landing pages for Trader to market paid search and website solutions</li>
                                    <li>Completed site customizations for a $2 million per year revenue client</li>
                                    <li>Optimized the speed of 9 sites belonging to Convertus' largest client, increasing PageSpeed Score by over 10x and decreasing load times by 10% </li>
                                    <li>Constructed platform-wide widgets and custom post types</li>
                                </ul>
                            </div>
                        </div>
                        <div className="resume__slider__entry">
                            <div className="resume__slider__entry__container">
                                <div className="resume__slider__entry__line">
                                    <div className="resume__slider__entry__line__location">
                                        180th Pacific Coast Scout Group
                                    </div>
                                    <div>
                                        Sep 2014 - Aug 2019
                                    </div>
                                </div>
                                <div className="resume__slider__entry__line">
                                    <div className="resume__slider__entry__line__position">
                                        Information Technology Department Member
                                    </div>
                                    <div className="resume__slider__entry__line__tech">
                                        PHP, HTML, CSS, Javascript
                                    </div>
                                </div>
                                <ul className="resume__slider__entry__list">
                                    <li>Created a web application using Basecamp3's API and OAuth2 to allow tracking of the RSVP status of all members for events</li>
                                    <li>Built a web application that tracks activity on Basecamp3 and generates a report, saving several hours per report</li>
                                    <li>Optimized the group site pccrovers.com, reducing load times by over 70%</li>
                                </ul>
                            </div>
                        </div>
                        <div className="resume__slider__entry">
                            <div className="resume__slider__entry__container">
                                <div className="resume__slider__entry__line">
                                    <div className="resume__slider__entry__line__location">
                                        University of British Columbia
                                    </div>
                                    <div>
                                        Sep 2014 - Apr 2018
                                    </div>
                                </div>
                                <div className="resume__slider__entry__line">
                                    <div className="resume__slider__entry__line__position">
                                        Bachelor of Applied Science
                                    </div>
                                    <div className="resume__slider__entry__line__tech">
                                        Computer Engineering
                                    </div>
                                </div>
                            </div>
                        </div>
                    </Slider>
                </div>

            </div>
        );
    }

}

export default Resume