import Slider from "react-slick"
import React from "react";
import { Timeline } from 'gsap/gsap-core';

class Portfolio extends React.Component {

    constructor(props) {
        super(props);

        this.next = this.next.bind(this);
        this.previous = this.previous.bind(this);
    }

    next() {
        this.slider.slickNext();
    }

    previous() {
        this.slider.slickPrev();
    }

    componentDidMount() {
        let tl = new Timeline();
        tl.set('.slick-list', {transform: 'translate(0%)'});

        this.listener = document.querySelector('.portfolio').addEventListener("wheel", function(e) {
            if ( e.deltaX >= 40 ) {
                this.next();
            } else if ( e.deltaX <= -40 ) {
                this.previous();
            }
        }.bind(this));
    }

    componentWillUnmount() {
        document.querySelector('.portfolio').removeEventListener("wheel", this.listener);
    }



    playVideo(e) {
        let vid = e.target.lastChild;
        if ( vid ) {
            if ( window.getComputedStyle(vid).getPropertyValue('display') !== 'none' ) {
                vid.play();
            }
        }
    }

    pauseVideo(e) {
        let vid = e.target.lastChild;
        if ( vid ) {
            vid.pause();
        }
    }


    render() {

        let settings = {
            dots: true,
            infinite: false,
            slidesToShow: 1,
            slidesToScroll: 1,
            centerMode: true,
            centerPadding: '20%',
            arrows: true,
            lazyLoad: 'progressive',
            responsive: [{
                breakpoint: 767,
                settings: {
                    centerPadding: '10%',
                }
            }]
        }


        return (
            <div className="portfolio">
                <div className="portfolio__slider">
                    <Slider ref={c => (this.slider = c)} {...settings}>
                        <div className="portfolio__slider__vid" onMouseOver={e => this.playVideo(e)} onMouseOut={e => this.pauseVideo(e)}>
                            <img src="/img/creative.png" alt="creative poster" />
                            <video muted loop>
                                <source src="/vid/CreativeEnergy1.mp4" type="video/mp4"></source>
                            </video>
                        </div>
                        <div className="portfolio__slider__vid" onMouseOver={e => this.playVideo(e)} onMouseOut={e => this.pauseVideo(e)}>
                            <img src="/img/lighthouse.png" alt="lighthouse poster"/>     
                            <video muted loop>
                                <source src="/vid/Lighthouse.mp4" type="video/mp4"></source>
                            </video>
                        </div>
                        <div className="portfolio__slider__vid" onMouseOver={e => this.playVideo(e)} onMouseOut={e => this.pauseVideo(e)}>
                            <img src="/img/senakw.png" alt="senakw poster" />
                            <video muted loop>
                                <source src="/vid/Senakw.mp4" type="video/mp4"></source>
                            </video>
                        </div>
                        <div className="portfolio__slider__vid" onMouseOver={e => this.playVideo(e)} onMouseOut={e => this.pauseVideo(e)}>
                            <img src="/img/riverside.png" alt="riverside poster" />
                            <video muted loop>
                                <source src="/vid/Riverside1.mp4" type="video/mp4"></source>
                            </video>
                        </div>
                        <div className="portfolio__slider__vid" onMouseOver={e => this.playVideo(e)} onMouseOut={e => this.pauseVideo(e)}>
                            <img src="/img/siegel.png" alt="siegel poster" />
                            <video muted loop>
                                <source src="/vid/Siegel.mp4" type="video/mp4"></source>
                            </video>
                        </div>
                        <div className="portfolio__slider__vid" onMouseOver={e => this.playVideo(e)} onMouseOut={e => this.pauseVideo(e)}>
                            <img src="/img/christmas.png" alt="christmas poster" />
                            <video muted loop>
                                <source src="/vid/ChristmasMarket.mkv"></source>
                            </video>
                        </div>
                    </Slider>
                </div>
            </div>
        );
    }
}

export default Portfolio