import React from 'react';
import './App.scss';

import { Timeline } from 'gsap/gsap-core';

// Pages
import Portfolio from './Components/Portfolio'
import Resume from './Components/Resume'
import Skills from './Components/Skills'

class App extends React.Component {


  constructor(props) {
    super(props);

    this.state = {
      page: 'home',
    }
  }

  componentDidMount() {
    let tl = new Timeline();
    tl.to('.menu', 1, {opacity: 1}, 0);
  }

  changePage( newpage ) {
    this.setState({
      page: newpage,
    })
  }

  render() {
    return (
      <div className="App">

        { this.state.page === 'portfolio' ? <Portfolio /> : ''}
        { this.state.page === 'resume' ? <Resume /> : ''}
        { this.state.page === 'skills' ? <Skills /> : ''}
        { this.state.page === 'home' ? 
            <div className="hero">
              <h3 className="hero__name">Nicholas Leung</h3>
              <h2 className="hero__title">developer.</h2>
              <div className="social">
                <a href="https://github.com/sansnickel" target="_blank"><div className="social__button"><img src="/img/github.svg" alt="github logo" /></div></a>
                <a href="https://www.linkedin.com/in/nleungky/" target="_blank"><div className="social__button"><img src="/img/linkedin.svg" alt="linkedin logo" /></div></a>
                <a href="mailto:nleungky@gmail.com"><div className="social__button"><img src="/img/mail.svg" alt="mail icon" /></div></a>
              </div>
            </div> : ''
        }

        <div className="menu">
          <div className={"menu__button menu__button--home" + ( this.state.page === 'home' ? ' active' : '' )} onClick={() => this.changePage( 'home' )}><img src="/img/home.svg" alt="home icon" /></div>
          <div className={"menu__button" + ( this.state.page === 'resume' ? ' active' : '' )} onClick={() => this.changePage( 'resume' )}><h3>Resume</h3></div>
          <div className={"menu__button" + ( this.state.page === 'skills' ? ' active' : '' )} onClick={() => this.changePage( 'skills' )}><h3>Skills</h3></div>
          <div className={"menu__button" + ( this.state.page === 'portfolio' ? ' active' : '' )} onClick={() => this.changePage( 'portfolio' )}><h3>Portfolio</h3></div>
        </div>
  

      </div>
  
  
  
      
    );
  }
}

export default App;
