import Slider from "react-slick"
import React from "react";
import gsap from 'gsap';
import { Timeline } from 'gsap/gsap-core';


class Skills extends React.Component {

    constructor(props) {
        super(props);

        this.next = this.next.bind(this);
        this.previous = this.previous.bind(this);
    }

    next() {
        this.slider.slickNext();
    }

    previous() {
        this.slider.slickPrev();
    }

    componentDidMount() {
        gsap.registerPlugin();
        let tl = new Timeline();
        tl.set('.slick-list', {transform: 'translate(0%)'});

        this.listener = document.querySelector('.skills').addEventListener("wheel", function(e) {
            if ( e.deltaX >= 40 ) {
                this.next();
            } else if ( e.deltaX <= -40 ) {
                this.previous();
            }
        }.bind(this));
    }

    
    componentWillUnmount() {
        document.querySelector('.skills').removeEventListener("wheel", this.listener);
    }

    render() {

        let settings = {
            dots: true,
            infinite: false,
            slidesToShow: 1,
            slidesToScroll: 1,
            centerMode: true,
            centerPadding: '20%',
            arrows: true,
            responsive: [{
                breakpoint: 767,
                settings: {
                    centerPadding: '10%',
                }
            }]
        }


        return (
            <div className="skills">

                <div className="skills__slider">

                    <Slider ref={c => (this.slider = c)} {...settings}>
                        <div className="skills__slider__entry">
                            <div className="skills__slider__entry__container">
                                <h3>Am Comfortable With...</h3>
                                <ul>
                                    <li>HTML</li>
                                    <li>CSS / SCSS</li>
                                    <li>Javascript (ES6)</li>
                                    <li>jQuery</li>
                                    <li>React.js</li>
                                    <li>Git</li>
                                    <li>PHP / Wordpress</li>
                                    <li>Java</li>
                                    <li>Node.js / Express.js</li>
                                    <li>XAMPP / MAMP</li>
                                    <li>pm2</li>
                                    <li>GSAP</li>
                                </ul>
                            </div>
                        </div>
                        <div className="skills__slider__entry">
                            <div className="skills__slider__entry__container">
                                <h3>Have Worked With...</h3>
                                <ul>
                                    <li>Next.js</li>
                                    <li>Vue.js</li>
                                    <li>Docker</li>
                                    <li>Heroku</li>
                                    <li>AWS</li>
                                    <li>C / C++</li>
                                    <li>SQL</li>
                                    <li>Android</li>
                                    <li>Python</li>
                                    <li>Firebase</li>
                                    <li>MongoDB + Mongoose</li>
                                </ul>
                            </div>
                        </div>
                    </Slider>
                </div>

            </div>
        );
    }

}

export default Skills